/* Main.css */

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto:wght@400;500&display=swap');

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to bottom right, #005f73, #0a9396, #94d2bd, #ca6702);
  color: white;
  font-family: 'Roboto', sans-serif;
  padding: 16px;
}

.heading {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 16px;
  opacity: 0;
  transform: translateY(-50px);
  animation: fadeInUp 1s forwards;
  font-family: 'Poppins', sans-serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); /* Add shadow for depth */
}

.heading2 {
  font-size: 2.8rem;
  font-weight: bold;
  margin-bottom: 24px;
  letter-spacing: 2px;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInDown 1s 0.5s forwards;
  font-family: 'Poppins', sans-serif;
}

.heading2 span {
  color: #fbbf24; /* Yellowish color */
}

.search-container {
  display: flex;
  border: 2px solid white;
  border-radius: 9999px;
  overflow: hidden;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
  transform: scale(0.8);
  animation: scaleIn 0.5s 1.2s forwards;
}

.search-container input {
  padding: 12px;
  font-size: 1rem;
  color: black;
  border: none;
  outline: none;
  width: 250px;
}

.search-container button {
  background-color: white;
  color: black;
  padding: 12px;
  border: none;
  cursor: pointer;
}

.peacock {
  font-size: 5rem;
  animation: rotatePeacock 3s infinite;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  to {
    transform: scale(1);
  }
}

@keyframes rotatePeacock {
  0% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-10deg);
  }
}

/* Animations */
@keyframes slideUp {
    to {
      transform: translateY(-80px);
      opacity: 1;
    }
  }
  
  @keyframes shrinkHeading {
    to {
      font-size: 1.5rem;
      margin-bottom: 0;
    }
  }
  
  @keyframes shrinkSearch {
    to {
      transform: scale(0.7);
    }
  }
  
  /* Navbar Transition Classes */
  .shrink-header .header {
    animation: slideUp 0.5s forwards;
  }
  
 .container {
  font-family: Arial, sans-serif;
  padding: 0;
  margin: 0;
}

.header {
  text-align: center;
  
  transition: all 0.5s ease-in-out;
}

.heading {
  font-size: 2.5rem;
  margin: 0;
}

.heading2 span {
  color: #f57c00;
  font-size: 2.2rem;
  font-weight: bold;
}

/* Styling for the search form */
.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
  max-width: 450px;
  background-color: #ffffff;
  border-radius: 50px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  padding: 12px 20px;
}

/* Input field */
.search-form input {
  padding: 12px 16px;
  width: 100%;
  font-size: 1rem;
  border: 2px solid #f57c00;
  border-radius: 30px;
  outline: none;
  transition: all 0.3s ease-in-out;
  font-family: 'Roboto', sans-serif;
}

/* Input field focus effect */
.search-form input:focus {
  border-color: #ff8f00;
  box-shadow: 0 0 8px rgba(255, 143, 0, 0.6);
}

/* Button */
.search-form button {
  padding: 12px 20px;
  margin-left: 10px;
  background-color: #f57c00;
  color: white;
  border: none;
  border-radius: 30px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/* Button hover effect */
.search-form button:hover {
  background-color: #e65100;
}

/* Optional: Add some space between the form and other elements */
.search-form {
  margin-top: 30px;
}


.results {
  margin: 40px;
  font-size: 1.2rem;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right, #fff7e6, #ffe0b3);
  padding: 15px 25px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  max-width: 900px;
  transition: all 0.3s ease;
}

.nav-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-right: 10px;
  color: #333;
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-search {
  display: flex;
  gap: 10px;
}

.nav-input {
  padding: 8px 12px;
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  width: 120px;
  transition: border-color 0.3s;
}

.nav-input:focus {
  border-color: #ffa500;
  outline: none;
}

.nav-button {
  padding: 10px 15px;
  background-color: #ffa500;
  color: white;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-left: -10px;
  transition: background-color 0.3s;
}

.nav-button:hover {
  background-color: #ff8800;
}

/* Results Styling */
.results {
  margin-top: 30px;
}

.results ul {
  list-style: none;
  padding: 0;
}

.results li {
  font-size: 1.2rem;
  margin: 8px 0;
}
.link-button {
  background: none;
  border: none;
  color: #0077cc;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  font-size: 1rem;
}

.link-button:hover {
  text-decoration: underline;
}
.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.product-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.product-card h4 {
  margin: 0;
  font-size: 1.1rem;
  color: #005f73;
}

.product-card .price {
  color: #008000;
  font-weight: bold;
  margin-top: 0.5rem;
}
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
  }

  .nav-title {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }

  .nav-search {
    width: 100%;
    margin-top: 10px;
  }

  .nav-input {
    width: calc(100% - 60px); /* Adjust input to fit */
  }

  .nav-button {
    width: 50px;
    height: 35px;
  }
}
.calculator {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.calculator input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  width: 100%;
  max-width: 200px;
  font-size: 1rem;
  text-align: center;
}

.calculator button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  max-width: 200px;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.calculator button:hover {
  background-color: #45a049;
}

.calculator p {
  font-size: 1rem;
  color: #333;
  margin-top: 5px;
}
